* {
  margin: 0;
  padding: 0;
}

body {
  background: #282b38;
}

.container {
  padding: 0;
  max-width: 1136px;
  margin: 0 auto;
}

.row {
  width: 100%;
  margin-left: auto;
  margin-right: auto;
}

.carousel-control-prev-icon {
  background-image: url("img/slider-vector-left.svg");
}

.carousel-control-next-icon {
  background-image: url("img/slider-vector-right.svg");
}

@font-face {
  font-family: Museo100;
  src: url(../fonts/MuseoSansCyrl-100.woff2) format("woff2"), url(../fonts/MuseoSansCyrl-100.woff) format("woff"), url(../fonts/MuseoSansCyrl-100.ttf) format("truetype");
  font-style: normal;
}

@font-face {
  font-family: Museo300;
  src: url(../fonts/MuseoSansCyrl-300.woff2) format("woff2"), url("../fonts/MuseoSansCyrl-300.woff") format("woff"), url("../fonts/MuseoSansCyrl-300.ttf") format("truetype");
  font-style: normal;
}

@font-face {
  font-family: Museo500;
  src: url(../fonts/MuseoSansCyrl-500.woff2) format("woff2"), url("../fonts/MuseoSansCyrl-500.woff") format("woff"), url("../fonts/MuseoSansCyrl-500.ttf") format("truetype");
  font-style: normal;
}

@font-face {
  font-family: Museo700;
  src: url("../fonts/MuseoSansCyrl-700.woff2") format("woff2"), url("../fonts/MuseoSansCyrl-700.woff") format("woff"), url("../fonts/MuseoSansCyrl-700.ttf") format("truetype");
  font-style: normal;
}

@font-face {
  font-family: Museo900;
  src: url("../fonts/MuseoSansCyrl-900.woff2") format("woff2"), url("../fonts/MuseoSansCyrl-900.woff") format("woff"), url("../fonts/MuseoSansCyrl-900.ttf") format("truetype");
  font-style: normal;
}

.orang-buton-with-vector {
  width: 14.5%;
  position: absolute;
  height: 65px;
  background-color: #e58d35;
  border-radius: 50px;
  color: #ffffff;
  display: flex;
  align-items: center;
  justify-content: space-between;
  font-size: 1rem;
}

.orang-buton-with-vector span {
  margin-left: 12%;
}

.orang-buton-with-vector img {
  margin-right: 2%;
}

.section-title {
  width: 100%;
  text-align: center;
}

.section-title h2 {
  position: relative;
  font-size: 2.2rem;
  text-transform: uppercase;
}

.section-title h2::after {
  content: "";
  position: absolute;
  border-bottom: 2px solid #e58d35;
  width: 8%;
  bottom: -20px;
  left: 45%;
  right: 45%;
  margin: auto;
}

.rectangle {
  height: 93px;
  width: 93px;
  background-color: #89c249;
  border-radius: 50px;
  outline: 1px solid #6c7188;
  outline-offset: 7px;
}

.element_carousel-indicators {
  margin-left: auto;
  margin-right: auto;
}

.element_carousel-indicators button {
  background-color: rgba(0, 0, 0, 0) !important;
  border: 2px solid #ffffff !important;
  border-radius: 50% !important;
  margin: 0 7px !important;
  width: 10px !important;
  height: 10px !important;
}

.element_carousel-indicators .active {
  background-color: #ffffff !important;
}

.prospect-card {
  width: 100%;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
  padding-left: 4%;
}

.prospect-card .prospects__prospect-card_img {
  border: 1px solid #6c7188;
  border-radius: 50%;
  height: 113px;
  width: 113px;
  display: flex;
  justify-content: center;
  align-items: center;
}

.prospect-card .prospects__prospect-card_img .image__prospect-card {
  border-radius: 50%;
  margin: 4%;
  background-color: #89c249;
  height: 93px;
  width: 93px;
  display: flex;
  justify-content: center;
  align-items: center;
}

.prospect-card .prospects__prospect-card_img .image__prospect-card img {
  height: 50px;
  width: 50px;
  margin-left: 5%;
}

.prospect-card .text__prospect-card {
  margin-top: 10%;
  min-height: 50px;
  display: flex;
  text-align: center;
}

.prospect-card .text__prospect-card p {
  font-size: 16px;
}

.slider-cards-element .carousel-inner .carousel-item.active,
.slider-cards-element .carousel-inner .carousel-item-next,
.slider-cards-element .carousel-inner .carousel-item-prev {
  display: flex;
}

.slider-cards-element .carousel-inner .carousel-item-end.active,
.slider-cards-element .carousel-inner .carousel-item-next {
  transform: translateX(33%);
}

.slider-cards-element .carousel-inner .carousel-item-start.active,
.slider-cards-element .carousel-inner .carousel-item-prev {
  transform: translateX(-33%);
}

.slider-cards-element .carousel-inner .carousel-item-end,
.slider-cards-element .carousel-inner .carousel-item-start {
  transform: translateX(0);
}

.slider-cards-element .carousel-control-prev {
  right: 100%;
  left: auto;
}

.slider-cards-element .carousel-control-next {
  left: 100%;
  right: auto;
}

.slider-cards-element .card {
  margin-left: -4%;
  background-color: rgba(0, 0, 0, 0);
  margin-right: 9%;
}

.header .header__navbar {
  height: 100px;
}

.header .header__navbar_button {
  border: none;
  margin: 21px 22px 0 0;
}

.header .header__navbar_button span {
  color: #fff;
}

.header .header__navbar_button:focus {
  border: none;
  box-shadow: none;
}

.header .header__navbar-collapse {
  z-index: 100;
}

.header .header__navbar-collapse .header__list {
  margin-top: 3px;
}

.header .header__navbar-collapse .header__list li {
  color: #ffffff;
}

.header .header__navbar-collapse .header__list li a.nav-link {
  letter-spacing: 0.07rem;
  color: #ffffff;
}

.header .header__button_block {
  padding-right: 3%;
}

.header .header__button_block .header__button {
  width: 169px;
  height: 48px;
  border-radius: 50px;
  color: #ffffff;
  background: #e58d35;
}

main {
  min-height: calc(100vh - 40px);
}

.footer h2 {
  color: rgb(0, 60, 255);
}

.main-home-page .slider {
  max-width: 1810px;
  height: 700px;
  margin: 0 auto;
  margin-top: 11px;
}

.main-home-page .slider .slider__img {
  height: 700px;
  width: 100%;
}

.main-home-page .slider .slider__carousel-caption {
  height: 78%;
  max-width: 100%;
  top: 0%;
  left: 19%;
  text-align: start;
  display: flex;
  align-items: center;
}

.main-home-page .slider .slider__carousel-caption .slider__caption_block {
  max-width: 40%;
}

.main-home-page .slider .slider__carousel-caption .slider__caption_block h2 {
  font-size: calc(1.475rem + 1.5vw);
  font-weight: 700;
  color: #ffffff;
  text-transform: uppercase;
  letter-spacing: 1px;
  padding-bottom: 15px;
}

.main-home-page .slider .slider__carousel-caption .slider__caption_block p {
  color: #fff;
  font-weight: 100;
  margin-bottom: 0;
}

.main-home-page .slider .slider__carousel-caption .slider__caption_block .carousel__carousel-caption-btn {
  width: 24.5%;
  position: absolute;
  height: 65px;
  margin-top: 21px;
  background-color: #e58d35;
  border-radius: 50px;
  color: #ffffff;
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.main-home-page .slider .slider__carousel-caption .slider__caption_block .carousel__carousel-caption-btn span {
  margin-left: 12%;
}

.main-home-page .slider .slider__carousel-caption .slider__caption_block .carousel__carousel-caption-btn img {
  margin-right: 2%;
}

.main-home-page .slider .slider__carousel-indicators {
  bottom: 8%;
}

.main-home-page .slider .slider__carousel-indicators button {
  width: 4px;
  height: 4px;
  margin: 0 12px;
  border-radius: 50%;
  background-color: rgba(0, 0, 0, 0);
  border: 2px solid #ffffff;
}

.main-home-page .slider .slider__carousel-indicators button.active {
  background-color: #ffffff;
}

.main-home-page .slider .carousel-control-next-icon {
  position: absolute;
  top: 40%;
  right: 50%;
  height: 142px;
  width: 53px;
}

.main-home-page .slider .carousel-control-prev-icon {
  position: absolute;
  top: 40%;
  left: 55%;
  height: 142px;
  width: 53px;
}

.main-home-page .calculation-order {
  margin-top: 0;
  position: relative;
}

.main-home-page .calculation-order .calculation-order__title {
  margin-top: 10%;
}

.main-home-page .calculation-order .calculation-order__title h2 {
  font-size: 2.2rem;
  color: #ffffff;
}

.main-home-page .calculation-order .calculation-order__title h2::after {
  content: "";
  border-top: 2px solid #89c249;
  width: 82px;
  display: inline-block;
  margin-bottom: 10px;
  margin-left: 19px;
}

.main-home-page .calculation-order .calculation-order__form-block {
  background-color: #363a4d;
  margin-top: 70px;
  height: 597px;
  max-width: 615px;
  border-radius: 20px;
  padding: 35px 50px;
}

.main-home-page .calculation-order .calculation-order__form-block .calculation-order__form .form-control::placeholder {
  font-size: 14px;
}

.main-home-page .calculation-order .calculation-order__form-block .calculation-order__form .form-label {
  color: #ffffff;
}

.main-home-page .calculation-order .calculation-order__form-block .calculation-order__form .form-check {
  min-height: 2.5rem;
}

.main-home-page .calculation-order .calculation-order__form-block .calculation-order__form .form-check .form-check-label {
  margin: 2px 0 0px 19px;
  color: #ffffff;
}

.main-home-page .calculation-order .calculation-order__form-block .calculation-order__form .form-check .form-check-input {
  width: 1.5em;
  height: 1.5em;
}

.main-home-page .calculation-order .calculation-order__form-block .calculation-order__form .form-check .form-check-input:checked {
  background-color: #ffffff;
  border-color: #ffffff;
}

.no-webp .main-home-page .calculation-order .calculation-order__form-block .calculation-order__form .form-check .form-check-input:checked[type=checkbox] {
  background-image: url("img/calculation_order/check-vector.png"); }

.main-home-page .calculation-order .calculation-order__form-block .calculation-order__form .form-drag-and-drop-file .drag-and-drop-file {
  display: flex;
  justify-content: center;
  align-items: center;
  max-width: 100%;
  border: 1px #6c7188 dashed;
  height: 40px;
  cursor: pointer;
}

.main-home-page .calculation-order .calculation-order__form-block .calculation-order__form .form-drag-and-drop-file .drag-and-drop-file span {
  font-size: 1rem;
  color: #ffffff;
}

.main-home-page .calculation-order .calculation-order__form-block .calculation-order__form .calculation-order__form-btn-submit {
  min-width: 230px;
  max-width: 400px;
}

.main-home-page .calculation-order .calculation-order__image-block {
  position: absolute;
  left: 50%;
}

.main-home-page .calculation-order .calculation-order__image-block img {
  width: 100%;
  height: 900px;
}

.main-home-page .prospects {
  background-color: #363a4d;
  width: 95%;
  margin: 4.5% auto 0px;
  border-radius: 18px;
  padding: 2.5% 15% 1% 16%;
  color: #ffffff;
  position: relative;
  z-index: 1000;
}

.main-home-page .prospects__prospects_title {
  margin-top: 2%;
}

.main-home-page .prospects__content {
  margin-top: 1%;
}

.main-home-page .delivery-options {
  color: #ffffff;
}

.main-home-page .delivery-options__title {
  margin-top: 4%;
}

.main-home-page .delivery-options__title h2::after {
  width: 5%;
  border-bottom: 2px solid #89c249;
}

@media screen and (max-width: 991px) {
  .section-title h2 {
    font-size: 24px;
  }

  .element_carousel-indicators button {
    margin: 0 13px !important;
    width: 4px !important;
    height: 4px !important;
  }

  .slider-cards-element .carousel-inner .carousel-item > div {
    display: none;
  }

  .slider-cards-element .carousel-inner .carousel-item > div:first-child {
    display: block;
  }

  .header .header__navbar {
    height: 77px;
    padding: 0;
    align-items: flex-start;
  }

  .header .navbar-brand .header_logo {
    height: 40px;
    width: 137px;
    margin: 15px 0 0px 15px;
  }

  .header .header__navbar-collapse {
    max-width: 100%;
    min-height: 520px;
    background-color: #363a4d;
    font-size: 1.5rem;
  }

  .header .header__navbar-collapse .header_nav_block {
    margin-top: 1rem;
  }

  .header .header__navbar-collapse .header_nav_block .header__list li {
    padding: 10px 0;
    width: 100%;
  }

  .header .header__navbar-collapse .header_nav_block .header__list li a.nav-link {
    letter-spacing: 0.07rem;
    color: #ffffff;
  }

  .header .header__button_block .header__button {
    margin-top: 10px;
    width: 300px;
    height: 66px;
  }

  .main-home-page .slider {
    margin: 0;
  }

  .main-home-page .slider .slider__img {
    height: 650px;
  }

  .main-home-page .slider .slider__carousel-caption {
    width: 100%;
    margin: 57px auto 0;
    height: 60%;
    padding: 0;
    display: block;
    top: 50%;
    left: 60%;
    transform: translate(-50%, -50%);
    text-align: center;
  }

  .main-home-page .slider .slider__carousel-caption .slider__caption_block {
    max-width: 70%;
  }

  .main-home-page .slider .slider__carousel-caption .slider__caption_block h2 {
    padding: 0;
    font-size: 30px;
  }

  .main-home-page .slider .slider__carousel-caption .slider__caption_block .carousel__carousel-caption-btn {
    margin-top: 8px;
    width: 300px;
  }

  .main-home-page .slider .slider__carousel-caption .slider__caption_block .carousel__carousel-caption-btn span {
    font-size: 16px;
  }

  .main-home-page .slider .carousel-control-next-icon {
    display: none;
  }

  .main-home-page .slider .carousel-control-prev-icon {
    display: none;
  }

  .main-home-page .calculation-order {
    margin-top: 0;
  }

  .main-home-page .calculation-order .calculation-order__title {
    display: flex;
    justify-content: center;
    text-align: center;
    position: relative;
    margin-top: 0;
  }

  .main-home-page .calculation-order .calculation-order__title h2 {
    font-size: 1.4rem;
    width: 75%;
  }

  .main-home-page .calculation-order .calculation-order__title h2::after {
    position: absolute;
    bottom: -24px;
    left: 45%;
    right: 45%;
    margin: 0px auto 0;
  }

  .main-home-page .calculation-order .calculation-order__form-block {
    margin: 70px auto 10px;
    width: 100%;
    height: 846px;
    padding: 35px 25px;
  }

  .main-home-page .calculation-order .calculation-order__form-block .calculation-order__form {
    margin-top: 10px;
  }

  .main-home-page .calculation-order .calculation-order__form-block .calculation-order__form-btn-submit {
    width: 270px;
    margin-top: 20px;
  }

  .main-home-page .prospects {
    margin-top: 15%;
    border-radius: 0%;
    padding: 10% 5% 3% 5%;
  }

  .main-home-page .prospects__prospects_title {
    margin-top: 1%;
  }

  .main-home-page .prospects__content {
    margin-top: 1%;
  }

  .main-home-page .prospects .prospects__prospect-card {
    margin-top: 20%;
    margin-bottom: 30%;
  }

  .main-home-page .prospects .prospects__carousel-indicators {
    bottom: 10%;
  }
}

@media screen and (max-width: 991px) and (max-width: 400px) {
  .main-home-page .slider .slider__carousel-caption .slider__caption_block .carousel__carousel-caption-btn {
    width: 80%;
  }

  .main-home-page .calculation-order .calculation-order__form-block .calculation-order__form-btn-submit {
    width: 80%;
  }
}

@media screen and (max-width: 991px) and (max-width: 500px) {
  .main-home-page .calculation-order .calculation-order__title h2::after {
    left: 40%;
  }
}

@media screen and (max-width: 1600px) and (min-width: 992px) {
  .main-home-page .slider .slider__carousel-caption .slider__caption_block {
    max-width: 60%;
  }

  .main-home-page .slider .slider__carousel-caption .slider__caption_block .carousel__carousel-caption-btn {
    width: 40%;
  }
}

@media screen and (max-width: 1250px) and (min-width: 991px) {
  .main-home-page .prospects {
    padding: 3% 10% 3% 12%;
    width: 97%;
  }
}

.webp .main-home-page .calculation-order .calculation-order__form-block .calculation-order__form .form-check .form-check-input:checked[type=checkbox] { background-image: url(img/calculation_order/check-vector.webp); }