.slider {
	@include Max_one(991px) {
		margin: 0;

		.slider__img {
			height: 650px;
		}

		.slider__carousel-caption {
			width: 100%;
			margin: 57px auto 0;
			height: 60%;
			padding: 0;
			display: block;
			top: 50%;
			left: 60%;
			transform: translate(-50%, -50%);
			text-align: center;

			.slider__caption_block {
				max-width: 70%;
				h2 {
					padding: 0;
					font-size: 30px;
				}

				.carousel__carousel-caption-btn {
					margin-top: 8px;
					width: 300px;
					span {
						font-size: 16px;
					}

					@include Max_one(400px) {
						width: 80%;
					}
				}
			}
		}
		.carousel-control-next-icon {
			display: none;
		}

		.carousel-control-prev-icon {
			display: none;
		}
	}
}
